@use '../foundation' as *;

.about__inner {
  padding:  rem(30)  rem(20);
}

.about__img {
  &:not(:last-child) {
    margin-bottom:  rem(50);
  }
}





.header {
  height: rem(40);
  background-color: #fff;
  @include mq("md") {
      height: rem(70);
  }
}

.header__inner {
  padding-left: rem(15);
  height: inherit;
  display: flex;
  justify-content: space-between;
  @include mq("md") {
      padding: 0 rem(50);
  }
}

.header__logo {
  max-width: rem(74);
  width: 100%;
  height: inherit;
  @include mq("md") {
      max-width: rem(100);
  }
}

.header__logo a {
  height: inherit;
  display: flex;
  align-items: center;
}

.header__logo img {
  width: 100%;
  height: 100%;
  object-fit: contain;
}

.header__nav {
  display: none;
  height: inherit;
  @include mq("md") {
      display: block;
  }
}

.header__nav-items {
  display: flex;
  height: inherit;
}

.header__nav-item {
  height: inherit;
}

.header__nav-item.header__nav-item--contact {
  margin-left: rem(15);
  display: flex;
  align-items: center;
}

.header__nav-item a {
  padding: 0 rem(15);
  height: inherit;
  display: flex;
  align-items: center;
  font-size: rem(15);
  font-weight: 500;
  line-height: 1;
  color: #333;
  text-transform: uppercase;
}

.header__nav-item.header__nav-item--contact a {
  padding: rem(10) rem(20);
  position: relative;
  height: initial;
  color: #fff;
  background-color: #DE8430;
}

.header__nav-item.header__nav-item--contact a::after {
  content: "";
  margin-left: rem(10);
  width: rem(11);
  height: rem(12);
  display: inline-block;
  background-image: url(./../images/common/contact-icon.svg);
  background-repeat: no-repeat;
  background-position: center;
  background-size: contain;
}

.header__hamburger {
  margin: 0;
  padding: 0;
  outline: none;
  border: none;
  position: relative;
  z-index: 999;
  width: rem(40);
  height: inherit;
  background-color: #DE8430;
  cursor: pointer;
  transition: .3s;
  @include mq("md") {
      display: none;
  }
}

.header__hamburger.is-open {
  background-color: transparent;
}

.header__hamburger span {
  position: relative;
  left: 50%;
  transform: translateX(-50%);
  display: block;
  width: rem(15);
  height: 1px;
  background-color: #fff;
  transition: .5s;
}

.header__hamburger span:nth-of-type(1) {
  top: rem(-4);
}

.header__hamburger span:nth-of-type(2) {
  top: 0;
}

.header__hamburger span:nth-of-type(3) {
  top: rem(4);
}

.header__hamburger.is-open span:nth-of-type(1) {
  top: 0;
  transform: translateX(-50%) rotate(45deg);
}

.header__hamburger.is-open span:nth-of-type(2) {
  opacity: 0;
}

.header__hamburger.is-open span:nth-of-type(3) {
  top: rem(-2);
  transform: translateX(-50%) rotate(-45deg);
}

.header__drawer {
  padding: rem(228) 0;
  display: none;
  position: absolute;
  z-index: 900;
  top: 0;
  right: 0;
  bottom: 0;
  left: 0;
  width: 100%;
  height: 100vh;
  background-color: rgba($color: #623A14, $alpha: .95);
  overflow-y: scroll;
  scrollbar-width: none;
}

.header__drawer::-webkit-scrollbar {
  display:none;
}

.header__drawer-item a {
  padding: rem(15) 0;
  display: block;
  font-size: rem(36);
  font-weight: 700;
  line-height: 1;
  letter-spacing: 0.05em;
  color: #fff;
  text-align: center;
  text-transform: uppercase;
}

.header__drawer-item.header__drawer-item--contact a::after {
  content: "";
  margin-bottom: rem(5);
  margin-left: rem(20);
  width: rem(15);
  height: rem(15);
  display: inline-block;
  background-image: url(./../images/common/contact-icon.svg);
  background-repeat: no-repeat;
  background-position: center;
  background-size: contain;
}
